<template>
  <v-stepper v-model="e1" class="elevation-4">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">{{ $t('wizard.create-user') }}</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2">{{ $t('wizard.choose-language') }}</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3">{{ $t('wizard.choose-theme') }}</v-stepper-step>
    </v-stepper-header>
    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card class="elevation-10 ma-3" color height="200px">
          <v-form>
            <v-text-field
              box
              prepend-icon="mdi-account"
              name="name"
              :label="$t('login.username')"
              type="text"
              v-model="username"
            ></v-text-field>
            <v-text-field
              counter
              box
              v-model="password"
              :label="$t('login.password')"
              loading
              prepend-icon="mdi-lock"
              name="password"
              :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'"
              :type="show ? 'text' : 'password'"
              @click:append="show = !show"
              :messages="[message]"
            >
              <v-progress-linear slot="progress" :value="progress" :color="color" height="4"></v-progress-linear>
            </v-text-field>
          </v-form>
        </v-card>
        <v-btn color="primary" @click="e1 = 2">{{$t("common.interaction.continue")}}</v-btn>
      </v-stepper-content>
      <v-stepper-content step="2">
        <v-card class="elevation-10 ma-3" color height="200px">
          <v-layout fill-height justify-center align-center>
            <v-flex xs12 class="pa-4" d-flex>
              <v-select
                :items="['en','de']"
                :label="$t('language.choose')"
                outline
                :value="language"
                @input="setLanguage"
              ></v-select>
            </v-flex>
          </v-layout>
        </v-card>
        <v-btn flat @click="e1 = 1">{{$t("common.interaction.back")}}</v-btn>
        <v-btn color="primary" @click="e1 = 3">{{$t("common.interaction.continue")}}</v-btn>
      </v-stepper-content>
      <v-stepper-content step="3">
        <v-card class="elevation-10 ma-3" color height="200px">
          <v-layout fill-height justify-center align-center>
            <v-flex xs12 class="pa-4" d-flex>
              <v-switch :label="$t('theme.dark')" :input-value="theme.dark" @change="setDark"></v-switch>
            </v-flex>
          </v-layout>
        </v-card>
        <v-btn flat @click="e1 = 2">{{$t("common.interaction.back")}}</v-btn>
        <v-btn color="primary">{{$t("common.interaction.finish")}}</v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  data() {
    return {
      e1: 0,
      username: "",
      password: "",
      show: false
    };
  },
  computed: {
    ...mapGetters({
      language: "settings/language",
      theme: "settings/theme"
    }),
    progress() {
      let score = this.password.length * 3;
      if (this.hasUppercaseLetters) {
        score += 10;
      }
      if (this.hasLowercaseLetters) {
        score += 10;
      }
      if (this.hasNumbers) {
        score += 15;
      }
      if (this.hasSymbols) {
        score += 15;
      }
      if (this.areWordsRepeated) {
        score -= 20;
      }
      if (this.areCharactersRepeated) {
        score -= 30;
      }
      if (!this.isLongEnough) {
        score = Math.min(50, score - 20);
      }
      return Math.min(100, Math.max(0, score));
    },
    color() {
      return ["error", "warning", "success"][Math.floor(this.progress / 40)];
    },
    hasLowercaseLetters() {
      return /[a-z]/.test(this.password);
    },
    hasUppercaseLetters() {
      return /[A-Z]/.test(this.password);
    },
    hasNumbers() {
      return /[0-9]/.test(this.password);
    },
    hasSymbols() {
      return /\W+/.test(this.password);
    },
    isLongEnough() {
      return this.password.length >= 8;
    },
    areCharactersRepeated() {
      return /^(.)\1{3,}$/.test(this.password);
    },
    areWordsRepeated() {
      return /^(.{4,}).*\1$/.test(this.password);
    },
    message() {
      if (this.areCharactersRepeated)
        return this.$t("login.password-security.no-same-characters");
      if (this.areWordsRepeated)
        return this.$t("login.password-security.no-repeated-words");
      if (this.password.length < 8)
        return this.$t("login.password-security.not-long-enough");
      if (!this.hasUppercaseLetters || !this.hasLowercaseLetters)
        return this.$t("login.password-security.no-uppercase-lowercase");
      if (!this.hasNumbers || !this.hasSymbols)
        return this.$t("login.password-security.no-numbers-and-special");
      return "";
    }
  },
  methods: {
    ...mapMutations({
      setLanguage: "settings/setLanguage",
      setDark: "settings/setDark"
    })
  }
};
</script>
