<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs10>
        <home-wizard/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import TheLoginBox from "../components/misc/TheLoginBox";
import { mapActions } from "vuex";
import HomeWizard from "../components/home/HomeWizard";

export default {
  components: {
    TheLoginBox,
    HomeWizard
  },
  methods: {
    async submit(data) {
      await this.authenticate(data);
      this.$router.push(
        this.$route.query.redirect ? this.$route.query.redirect : "/"
      );
    },
    ...mapActions({
      authenticate: "auth/authenticate"
    })
  }
};
</script>
