<template>
  <v-card class="elevation-12">
    <v-toolbar dark color="primary">
      <v-toolbar-title>{{$t("login.prompt")}}</v-toolbar-title>
    </v-toolbar>
    <v-card-text>
      <v-flex xs12>
        <logo/>
      </v-flex>
      <v-form>
        <v-text-field
          box
          prepend-icon="mdi-account"
          name="name"
          :label="$t('login.username')"
          type="text"
          v-model="username"
        ></v-text-field>
        <v-text-field
          counter
          box
          v-model="password"
          :label="$t('login.password')"
          prepend-icon="mdi-lock"
          name="password"
          :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'"
          :type="show ? 'text' : 'password'"
          @click:append="show = !show"
        ></v-text-field>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-btn
        color="primary"
        block
        large
        @click="$emit('submit', {password, username})"
        :disabled="!password || !username"
      >{{$t("login.login")}}</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import Logo from "./Logo";

export default {
  components: {
    Logo
  },
  data: () => ({
    username: "",
    password: "",
    show: false
  }),
  props: {}
};
</script>